<script>
export default {
  data: () => ({
    alert: { show: false },
    admin: { usrname: "" },
    actorList: [],
    addDialog: false,
    editDialog: false,
    actor: {},
    img: {},
    actorToEdit: {},
    helpDialog: { show: false, img: "", type: "" },
    actorToDelete: { dialog: false, actor: {} },
    langDialog: { open: false, add: false },
    actorLangs: [{}],
    newLang: {},
    isLangEdit: false,
    smed: {
      dialog: false,
      id_actor: 0,
      fb: null,
      tw: null,
      ig: null,
      yt: null,
    },
    newSmed: true,
  }),
  created: async function () {
    let session = JSON.parse(sessionStorage.getItem("sesioncda"));
    if (session == null || session.usrname != "admin") {
      this.$router.push("/adminlogin");
    }
    this.admin = session;
    this.loadAllActors();
  },
  methods: {
    async loadAllActors() {
      try {
        const res = await this.axios.get("/actor/get");
        this.actorList = res.data.allActors;
      } catch (error) {
        console.log(error);
      }
    },
    async readActor(id) {
      try {
        const res = await this.axios.get(`/actor/get/${id}`);
        this.actorToEdit = res.data.actor;
        this.actorToEdit.sexo = this.sexValue(this.actorToEdit.sexo);
        this.actorToEdit.fecha_edad = this.dateRegex(
          this.actorToEdit.fecha_edad
        );
        this.editDialog = true;
      } catch (error) {
        console.log(error);
      }
    },
    async addActor() {
      let valid = this.$refs.actorForm.validate();
      if (valid) {
        try {
          this.actor.sexo = this.sexValue(this.actor.sexo);
          this.actor.estatura = this.comaToDot(this.actor.estatura);
          const res = await this.axios.post("/actor/create", this.actor);
          //TODO: asignar imagen subida al objeto actor
          if (this.uploadMainImg(this.img.main, this.actor.id)) {
            this.$refs.actorForm.reset();
            this.addDialog = false;
            this.alert = {
              show: true,
              type: "success",
              message: res.data.message,
            };
          }
        } catch (error) {
          console.log(error);
        }
      }
    },
    async uploadMainImg(file, id) {
      const imgToUpload = new FormData();
      //TODO: Testear diferencia con update!!!
      imgToUpload.append("actor_img", file);
      try {
        const res = await this.axios.post(
          `/actor/img/create/${id}`,
          imgToUpload
        );
        return true;
      } catch (error) {
        console.log(error);
        return false;
      }
    },
    async editActor() {
      let valid = this.$refs.actorForm.validate();
      if (valid) {
        try {
          this.actorToEdit.sexo = this.sexValue(this.actorToEdit.sexo);
          this.actorToEdit.estatura = this.comaToDot(this.actorToEdit.estatura);
          const res = await this.axios.put(
            `/actor/update/${this.actorToEdit.id}`,
            this.actorToEdit
          );
          this.editDialog = false;
          this.alert = {
            show: true,
            type: "success",
            message: res.data.message,
          };
          this.loadAllActors();
        } catch (error) {
          console.log(error);
        }
      }
    },
    async deleteActor(id) {
      try {
        const res = await this.axios.delete(`/actor/delete/${id}`);
        this.alert = {
          show: true,
          type: "info",
          message: res.data.message,
        };
      } catch (error) {
        console.log(error);
      }
      this.actorToDelete.dialog = false;
      this.loadAllActors();
    },
    sexValue(sex) {
      let value;
      switch (sex) {
        case "Masculino":
          value = "m";
          break;
        case "Femenino":
          value = "f";
          break;
        case "m":
          value = "Masculino";
          break;
        case "f":
          value = "Femenino";
          break;
        default:
          value = "No asignado";
          break;
      }
      return value;
    },
    dateRegex(date) {
      const rgx = /T\d\d:00:00\.000Z/;
      return date.replace(rgx, "");
    },
    helpDiag(help) {
      this.helpDialog.img = help;
      this.helpDialog.show = true;
    },
    comaToDot(txt) {
      return txt.replace(/,/g, ".");
    },
    async showLangs(actorId) {
      this.newLang.id = actorId;
      try {
        const res = await this.axios.get(`/actor/lang/get/${actorId}`);
        this.actorLangs = res.data.query;
      } catch (error) {
        console.log(error);
      }
      this.langDialog.open = true;
    },
    async saveNewLang() {
      if (this.isLangEdit) {
        try {
          const res = await this.axios.put(`/actor/lang/update`, this.newLang);
          if (res.status === 200) {
            this.langDialog.add = false;
          }
        } catch (error) {
          console.log(error);
        }
      } else {
        try {
          const res = await this.axios.post(`/actor/lang/insert`, this.newLang);
          if (res.status === 200) {
            this.langDialog.add = false;
          }
        } catch (error) {
          console.log(error);
        }
      }
    },
    async deleteLang(langId) {
      try {
        const res = await this.axios.delete(`/actor/lang/delete/${langId}`);
        if (res.status === 200) {
          this.showLangs(this.newLang.id);
        }
      } catch (error) {
        console.log(error);
      }
    },
    async editLang(langId) {
      this.newLang = this.actorLangs.find((lang) => lang.id == langId);
      console.log(this.newLang);
      this.langDialog.add = true;
    },
    async openSocial(actorId) {
      const res = await this.axios.get(`/actor/sm/get/${actorId}`);
      if (res.data.rrss == null) {
        this.newSmed = true;
      } else {
        this.newSmed = false;
        this.smed.tw = res.data.rrss.tw;
        this.smed.fb = res.data.rrss.fb;
        this.smed.yt = res.data.rrss.yt;
        this.smed.ig = res.data.rrss.ig;
      }
      this.smed.id_actor = actorId;
      this.smed.dialog = true;
    },
    async closeSocial() {
      this.smed.tw = "";
      this.smed.fb = "";
      this.smed.yt = "";
      this.smed.ig = "";
      this.smed.dialog = false;
    },
    async saveSocial() {
      console.log(this.smed.id_actor);
      if (this.newSmed) {
        try {
          const res = await this.axios.post(`/actor/sm/insert`, this.smed);
          if (res.status === 200) {
            this.smed.dialog = false;
            this.smed.tw = null;
            this.smed.fb = null;
            this.smed.yt = null;
            this.smed.ig = null;
            this.alert = {
              show: true,
              type: "succes",
              message: res.data.message,
            };
          }
        } catch (error) {
          this.smed.dialog = false;
          this.alert = {
            show: true,
            type: "error",
            message: error.data.message,
          };
        }
      } else {
        try {
          const res = await this.axios.put(`/actor/sm/update`, this.smed);
          if (res.status === 200) {
            this.alert = {
              show: true,
              type: "succes",
              message: res.data.message,
            };
          }
        } catch (error) {
          this.smed.dialog = false;
          this.alert = {
            show: true,
            type: "error",
            message: error.data.message,
          };
        }
        this.closeSocial();
      }
    },
  },
};
</script>