<template>
  <v-container>
    <v-alert text="" v-model="alert.show" :type="alert.type" dismissible>
      {{ alert.message }}
    </v-alert>
    <v-row justify="center">
      <v-card class="mt-15 pa-5">
        <v-card-title>
          Panel de Administracin
        </v-card-title>
        <v-card-text>
          <v-form ref="logInForm" @submit.prevent="logIn()">
            <v-text-field label="Usuario" prepend-icon="fa-user" v-model="user.usrname" :rules="inputRule">
            </v-text-field>
            <v-text-field label="Contrasea" prepend-icon="fa-lock" type="password" v-model="user.psswd"
              :rules="inputRule"></v-text-field>
            <v-col class="text-center">
              <v-btn block class="success" type="submit">Conectar</v-btn>
            </v-col>
          </v-form>
        </v-card-text>
      </v-card>
    </v-row>

  </v-container>
</template>

<script>
export default {
  data: () => ({
    alert: {
      show: false, message: ''
    },
    inputRule: [
      value => !!value || 'Los campos no pueden estar vacios'
    ],
    user: { usrname: '', psswd: '' }
  }),
  methods: {
    async logIn() {
      let valid = this.$refs.logInForm.validate();
      if (valid) {
        try {
          const res = await this.axios.post('/logIn', this.user);
          let msg = '';
          if (!res.data.NotFound) {
            sessionStorage.setItem('sesioncda', JSON.stringify(res.data.admin));
            this.$router.push('/adminpanel');
          }
          this.$refs.logInForm.reset();
        } catch (error) {
          this.alert = {
            show: true,
            type: 'error',
            message: error.response.data.message
          }
        }
      }
    }
  },
}
</script>