<template>
  <v-container>
    <v-app-bar flat outlined app justify="center">
      <v-btn
        @click="$router.push(`/adminpanel`)"
        color="primary"
        class="ml-5"
        outlined
      >
        <v-icon left>fa-angles-left</v-icon>
        Volver
      </v-btn>
      <v-spacer></v-spacer>
      <div justify>Imgenes de {{ actor.nombre }}</div>
      <v-spacer></v-spacer>

      <v-btn @click="upload.dialog = true" color="primary" outlined>
        <v-icon left>fa-plus</v-icon>
        Imagen
      </v-btn>
    </v-app-bar>
    <v-alert text v-model="alert.show" :type="alert.type" dismissible>{{
      alert.message
    }}</v-alert>
    <v-row justify="center">
      <v-card
        class="ma-3"
        max-width="344"
        v-for="img in imageList"
        :key="img.id"
      >
        <v-img height="200" v-bind:src="img.uri_foto">
          <v-btn
            color="green accent-4"
            class="ma-auto"
            fab
            large
            icon
            @click="mainImgStatus(img.id)"
          >
            <v-icon v-if="img.img_principal == true">fa-thumbtack</v-icon>
            <v-icon v-else>fa-circle</v-icon>
          </v-btn>
        </v-img>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn
            color="blue"
            class="ma-auto"
            dark
            plain
            @click="showImgStatus(img.mostrar, img.id)"
          >
            <v-icon v-if="img.mostrar == true">fa-check</v-icon>
            <v-icon v-else>fa-xmark</v-icon>
          </v-btn>
          <v-divider vertical></v-divider>
          <v-btn
            color="red"
            class="ma-auto"
            dark
            plain
            @click="
              () => {
                imageToDelete.image = img;
                imageToDelete.dialog = true;
              }
            "
            ><v-icon>fa-trash</v-icon></v-btn
          >
        </v-card-actions>
      </v-card>
    </v-row>
    <div class="text-center">
      <v-dialog v-model="upload.dialog" width="500">
        <v-card>
          <v-card-title class="text-h5 grey lighten-2">
            Subir nueva imagen de {{ actor.nombre }}
          </v-card-title>

          <v-card-text>
            <v-file-input
              placeholder="Selecciona una imagen"
              label="Subir imagen"
              prepend-icon="fa-camera"
              v-model="upload.img"
              required
            ></v-file-input>
          </v-card-text>
          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="error" outlined @click="upload.dialog = false">
              Cerrar
            </v-btn>
            <v-btn color="primary" outlined @click="uploadImg(upload.img)">
              Subir
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
    <div class="text-center">
      <v-dialog v-model="imageToDelete.dialog" width="250">
        <v-card>
          <v-card-title class="text-h5 grey lighten-2">
            Borrar imagen?
          </v-card-title>
          <v-divider></v-divider>
          <v-card-actions justify="center">
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              outlined
              @click="imageToDelete.dialog = false"
            >
              Cerrar
            </v-btn>
            <v-btn
              color="error"
              outlined
              @click="deleteImage(imageToDelete.image)"
            >
              Borrar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </v-container>
</template>
<style scoped>
#logo {
  font-family: "Wire One";
  font-size: 2em;
}
</style>
<script>
export default {
  data: () => ({
    alert: { show: false },
    admin: { usrname: "" },
    actor: {},
    mainImg: {},
    imageList: [],
    upload: { dialog: false },
    imageToDelete: { dialog: false, image: {} },
  }),
  created: async function () {
    let session = JSON.parse(sessionStorage.getItem("sesioncda"));
    if (session == null || session.usrname != "admin") {
      this.$router.push("/adminlogin");
    }
    this.admin = session;
    this.axios
      .get(`/actor/get/${this.$route.params.id_a}`)
      .then((res) => {
        this.actor = res.data.actor;
      })
      .catch((e) => {
        console.log(e);
      });
    this.loadImages(this.$route.params.id_a);
  },
  methods: {
    async mainImgStatus(imageId) {
      this.mainImg.actor = this.actor.id;
      this.mainImg.image = imageId;
      this.axios
        .post(`/actor/img/chg/main/`, this.mainImg)
        .then((res) => {
          this.alert.type = "success";
          this.alert.message = res.data.message;
          this.loadImages(this.actor.id);
        })
        .catch((e) => {
          this.alert.type = "error";
          this.alert.message = e.data.message;
        });
      this.alert = {
        show: true,
      };
    },
    async uploadImg(file) {
      const imgToUpload = new FormData();
      imgToUpload.append("new_img", file);
      console.log(imgToUpload);
      this.axios
        .post(`/actor/img/new/${this.actor.id}`, imgToUpload)
        .then((res) => {
          this.upload.dialog = false;
          this.alert.type = "success";
          this.alert.message = res.data.message;
          this.loadImages(this.actor.id);
        })
        .catch((e) => {
          this.alert.type = "error";
          this.alert.message = e.data.message;
        });
      this.alert = {
        show: true,
      };
      this.loadImages(this.actor.id);
    },
    async loadImages(actor) {
      this.axios
        .get(`/actor/img/get/${actor}`)
        .then((res) => {
          this.imageList = res.data.img;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    async deleteImage(img) {
      if (img.img_principal != true) {
        this.axios
          .delete(`/actor/img/delete/${img.id}`)
          .then((res) => {
            this.alert = {
              type: "success",
              message: res.data.message,
              show: true,
            };
          })
          .catch((e) => {
            this.alert.type = "error";
            this.alert.message = e.data.message;
          });
          this.loadImages(this.actor.id);
      } else {
        this.alert = {
          type: "error",
          message:
            "No puedes borrar la imagen principal! Cmbiala por otra y luego brrala",
          show: true,
        };
      }
      this.imageToDelete.dialog = false;
    },
  },
};
</script>