    <v-main class="pa-0">
      <v-card elevation="0" height="100vh" class="ma-6">
        <v-row>
          <v-tabs fixed-tabs light color="grey darken-3">
            <v-tab>Informacin</v-tab>
            <v-tab-item>
              <v-row class="mt-1">
