                  <template>
                    <v-carousel>
                      <v-carousel-item
                        v-for="(eachImg, imgk) in allImgs.remainingImgs"
                        :key="imgk"
                        :src="eachImg"
                        reverse-transition="fade-transition"
                        transition="fade-transition"
                      ></v-carousel-item>
                    </v-carousel>
                  </template>
                </v-col>
                <v-col
                  cols="12"
                  sm="12"
                  md="6"
                  lg="6"
                  xl="6"
                  class="text-center"
                >
                  <div
                    rounded
                    class="overline text-truncate text-h6 mb-4 font-weight-bold"
                  >
                    {{ actor.nombre }} <v-spacer></v-spacer>           
                    <v-btn icon x-small class="ma-3" v-if="socialMedia.tw" @click="openSm('tw')"><v-icon>fa-twitter</v-icon></v-btn>
                    <v-btn icon x-small class="ma-3" v-if="socialMedia.fb" @click="openSm('fb')"><v-icon>fa-facebook</v-icon></v-btn>
                    <v-btn icon x-small class="ma-3" v-if="socialMedia.yt" @click="openSm('yt')"><v-icon>fa-youtube</v-icon></v-btn>
                    <v-btn icon x-small class="ma-3" v-if="socialMedia.ig" @click="openSm('ig')"><v-icon>fa-instagram</v-icon></v-btn>
                  </div>
                  <v-divider></v-divider>
                  <v-row>
                    <v-col cols="6">
                      <v-list disabled three-line>
                        <v-list-item-group color="primary">
                          <v-list-item
                            v-for="(car, i) in caracteristicas"
                            :key="i"
                          >
                            <v-list-item-icon>
                              <v-icon v-text="car.icon"></v-icon>
                            </v-list-item-icon>
                            <v-list-item-content class="text-h6 text-left py-4">
                              <v-list-item-title
                                v-text="car.text"
                              ></v-list-item-title>
                              <v-list-item-subtitle
                                v-text="car.info"
                              ></v-list-item-subtitle>
                            </v-list-item-content>
                          </v-list-item>
                        </v-list-item-group>
                      </v-list>
                    </v-col>
                    <v-col cols="6">
                      <v-list disabled three-line>
                        <v-list-item-group>
                          <v-list-item v-for="(sk, k) in skills" :key="k">
                            <v-list-item-content class="text-center">
                              <v-list-item-title
                                class="text-h5 font-weight-light py-8"
                                v-text="sk"
                              />
                            </v-list-item-content>
                          </v-list-item>
                        </v-list-item-group>
                      </v-list>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-tab-item>
            <template v-for="(tab, tabkey) in tabs">
              <v-tab :key="tabkey"> {{ tab.tab }}</v-tab>
              <v-tab-item :key="tabkey"> 
              <v-row v-show="(tab.tab == 'vdeos')">
                  <v-flex
                    v-for="vid in videoList"
                    :key="vid.id"
                    class="d-inline-flex justify-space-around"
                  >
                    <v-card class="ma-10" max-width="370" elevation="6">
                      <v-img height="190" v-bind:src="vid.img" >
                        <v-btn 
                        elevation="6" 
                        large color="red" top absultute dark :style="{left: '50%', transform:'translateX(-50%)',}" class="mt-15"
                        @click="ytOpen(vid.uri_video)"
                        >
                          <v-icon> fa-play </v-icon>
                        </v-btn>
                      </v-img>
                    </v-card>
                  </v-flex>
                </v-row>           
                <v-list
                  dense
                  v-for="(inf, ikey) in tab.info"
                  :key="ikey"
                  class="text-truncate"
                >
                  <v-list-item v-if="inf.type == true">
                    <v-list-item-content>
                      <h3>
                        <v-icon x-small>fa-certificate</v-icon>&nbsp;{{
                          inf.text
                        }}
                      </h3>
                    </v-list-item-content>
                  </v-list-item>
                  <v-divider v-if="inf.type == true"></v-divider>
                  <v-list-item v-else>
                    <v-list-item-content>
                      <v-list-item-title
                        ><strong>{{ inf.text }}</strong></v-list-item-title
                      >
                      <v-list-item-subtitle>{{
                        inf.date
                      }}</v-list-item-subtitle>
                    </v-list-item-content>
                  </v-list-item>
                </v-list>
              </v-tab-item>
            </template>
          </v-tabs>
        </v-row>
        <v-row>
          <v-col cols="12" sm="12" md="6" lg="6" xl="6">
            <v-row v-for="(lang, l) in langs" :key="l">
              <div class="overline">
                <h3>{{ lang.idioma }}</h3>
              </div>
              <v-progress-linear
                height="20"
                :value="lang.bar"
                color="grey lighten-1"
              >
                <strong>{{ Math.ceil(lang.bar) }}%</strong>
              </v-progress-linear>
            </v-row>
          </v-col>
          <v-col cols="12" sm="6" md="6" lg="6" xl="6">
            <v-flex
              class="d-inline-flex justify-end"
              v-for="actor in actorList"
              :key="actor.id"
            >
              <v-col cols="12">
                <v-card
                  class="rounded-0"
                  max-width="140"
                  color="grey lighten-5"
                  elevation="0"
                >
                  <router-link :to="`/perfil/actor/${actor.url}/${actor.id}`">
                    <v-img
                      v-bind:src="actor.mainImg"
                      class="actorImg"
                      max-height="165"
                      max-width="155"
                    >
                      <div class="showName text-center">
                        <v-flex align-self="center">
                          ______________
                          <v-divider></v-divider>

                          {{ actor.nombre }}
                          <v-divider class="mt-n3"></v-divider>
                          ______________
                        </v-flex>
                      </div>
                    </v-img>
                  </router-link>
                </v-card></v-col
              >
            </v-flex>
          </v-col>
        </v-row>
      </v-card>
    </v-main>
  </v-container>
</template>
<style scoped>
