<script>
import TopBar from "@/components/TopBar.vue";

export default {
  components: {
    TopBar,
  },
  data: () => ({
    actor: {},
    img: { main: "", w: 800, h: 600 },
    caracteristicas: [
      { text: "Edad", icon: "fa-calendar", info: "" },
      { text: "Estatura", icon: "fa-ruler-vertical", info: "" },
      { text: "Cabello", icon: "fa-scissors", info: "" },
      { text: "Ojos", icon: "fa-eye", info: "" },
      { text: "Idiomas", icon: "fa-language", info: "" },
    ],
    skills: [],
    tabs: [],
    tabList: null,
    tabCount: 0,
    allImgs: [{}],
    langs: [{}],
    videoList: [],
    actorList: [],
    socialMedia: {},
    cfg: "menubar=yes,location=yes,resizable=yes,scrollbars=yes,status=yes"
  }),
  created: async function () {
    this.axios
      .get("/actor/get")
      .then((res) => {
        this.actorList = res.data.allActors;
      })
      .catch((e) => {
        console.log(e);
      });

    this.axios
      .get(`/actor/lang/get/${this.$route.params.id_a}`)
      .then((res) => {
        this.langs = res.data.query;
      })
      .catch((e) => {
        console.log(e);
      });

    this.axios
      .get(`/actor/vid/get/${this.$route.params.id_a}`)
      .then((res) => {
        this.videoList = res.data.vids;
      })
      .catch((e) => {
        console.log(e);
      });

    this.axios
      .get(`/actor/sm/get/${this.$route.params.id_a}`)
      .then((res) => {
        this.socialMedia = res.data.rrss;
      })
      .catch((e) => {
        console.log(e);
      })


    this.axios
      .get(`/actor/get/${this.$route.params.id_a}`)
      .then((res) => {
        this.actor = res.data.actor;

        this.caracteristicas[0].info = this.actor.fecha_edad;
        this.caracteristicas[1].info = this.actor.estatura;
        this.caracteristicas[2].info = this.actor.cabello;
        this.caracteristicas[3].info = this.actor.ojos;
        this.caracteristicas[4].info = this.langsByComa(this.langs);

        this.skills = this.actor.habilidades.split("/");

        if (this.actor.formacion != null) {
          if (this.actor.formacion.length > 1) {
            this.tabs[this.tabCount] = {
              tab: "formacin",
              info: this.actor.formacionJson,
            };
            this.tabCount++;
          }
        }

        if (this.actor.experiencia != null) {
          if (this.actor.experiencia.length > 1) {
            this.tabs[this.tabCount] = {
              tab: "experiencia",
              info: this.actor.experienciaJson,
            };
            this.tabCount++;
          }
        }

        if (this.actor.premios != null) {
          if (this.actor.premios.length > 1) {
            this.tabs[this.tabCount] = {
              tab: "premios",
              info: this.actor.premiosJson,
            };
            this.tabCount++;
          }
        }

        if (this.videoList != null) {
          this.tabs[this.tabCount] = {
            tab: "vdeos",
            info: true,
          };
          this.tabCount++;
        }
      })
      .catch((e) => {
        console.log(e);
      });

    this.axios
      .get(
        `/actor/img/getRemain/${this.img.w}/${this.img.h}/${this.$route.params.id_a}`
      )
      .then((res) => {
        this.allImgs = res.data;
      })
      .catch((e) => {
        console.log(e);
      });

    this.axios
      .get(
        `/actor/img/getMain/${this.img.w}/${this.img.h}/${this.$route.params.id_a}`
      )
      .then((res) => {
        this.img.main = res.data.cloudinaryImg;
        this.allImgs.remainingImgs.unshift(this.img.main);
      })
      .catch((e) => {
        console.log(e);
      });
    
  },
  methods: {
    langsByComa(fullLangsObj) {
      let langs = "";
      fullLangsObj.forEach((l) => (langs += l.idioma + " "));
      return langs;
    },
    ytOpen(video){
      window.open(video, this.cfg);
    },
    openSm(sm){
      console.log(this.cfg);
      switch (sm) {
        case 'tw':
          window.open(this.socialMedia.tw, this.cfg);
          break;
        case 'yt':
          window.open(this.socialMedia.yt, this.cfg);
          break;
        case 'ig':
          window.open(this.socialMedia.ig, this.cfg);
          break;
        case 'fb':
          window.open(this.socialMedia.fb, this.cfg);
          break;
        default:
          window.open('http://www.xavi.tech', this.cfg);
          break;
      }
    }
  },
};
</script>