<template>
  <v-container>
    <top-bar />
    <v-main>
      <v-container>
        <v-row justify="center">
          <v-form ref="contactForm" @submit.prevent="submit()">
            <v-alert
              text=""
              v-model="alert.show"
              :type="alert.type"
              dismissible
            >
              {{ alert.message }}
            </v-alert>
            <v-text-field
              outlined
              v-model="form.name"
              label="Nombre"
              required
              class="inputWidth"
            ></v-text-field>
            <v-text-field
              outlined
              v-model="form.email"
              label="E-mail"
              required
            ></v-text-field>
            <v-select
              outlined
              v-model="form.subject"
              :items="items"
              label="Asunto"
              required
            ></v-select>
            <v-textarea
              outlined
              v-model="form.msg"
              :counter="500"
              label="Mensaje"
              hint="Mximo 500 letras"
            ></v-textarea>

            <v-btn class="mr-4" type="submit"> Enviar </v-btn>
            <v-btn @click="this.$refs.contactForm.reset()"> Reiniciar </v-btn>
          </v-form>
        </v-row>
      </v-container>
    </v-main>
  </v-container>
</template>

<style>
.inputWidth {
  max-width: 100vw;
  min-width: 55vw;
}
</style>

<script>
import TopBar from "@/components/TopBar.vue";
export default {
  components: {
    TopBar,
  },
  data: () => ({
    items: ["Contratacin", "Formar parte", "Prensa", "Otros"],
    form: { name: "", mail: "", subject: "", msg: "" },
    alert: { show: false, message: "", type: "" },
  }),
  methods: {
    async submit() {
      try {
        const res = await this.axios.post("/sendForm", this.form);
        this.alert = {
          show: true,
          type: "success",
          message:
            "Mensaje envado correctamente! Pronto recibirs una respuesta",
        };
      } catch (error) {
        this.alert = {
          show: true,
          type: "error",
          message:
            "Ha ocurrido un error enviando el mensaje, enva un mail a: mail@correo.es",
        };
      }
    },
  },
};
</script>