<template>
  <v-container>
    <v-app-bar flat outlined app justify="center">
      <v-btn
        @click="$router.push(`/adminpanel`)"
        color="primary"
        class="ml-5"
        outlined
      >
        <v-icon left>fa-angles-left</v-icon>
        Volver
      </v-btn>
      <v-spacer></v-spacer>
      <div justify>Vdeos de {{ actor.nombre }}</div>
      <v-spacer></v-spacer>

      <v-btn
        color="primary"
        outlined
        @click="
          () => {
            video.dialog = true;
            isEdit = false;
          }
        "
      >
        <v-icon left>fa-plus</v-icon>
        Video
      </v-btn>
    </v-app-bar>
    <v-alert text v-model="alert.show" :type="alert.type" dismissible>{{
      alert.message
    }}</v-alert>
    <v-row justify="center">
      <v-card
        class="ma-3"
        max-width="344"
        v-for="vid in videoList"
        :key="vid.id"
      >
        <v-img height="200" v-bind:src="vid.img"> </v-img>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn
            color="blue"
            class="ma-auto"
            dark
            plain
            @click="editVideo(vid.id)"
          >
            <v-icon>fa-pencil</v-icon>
          </v-btn>
          <v-divider vertical></v-divider>
          <v-btn
            color="red"
            class="ma-auto"
            dark
            plain
            @click="
              () => {
                videoToDelete.id = vid.id;
                videoToDelete.dialog = true;
              }
            "
            ><v-icon>fa-trash</v-icon></v-btn
          >
        </v-card-actions>
      </v-card>
    </v-row>
    <div class="text-center">
      <v-dialog width="500" v-model="video.dialog">
        <v-card>
          <v-card-title class="text-h5 grey lighten-2">
            Nuevo vdeo de {{ actor.nombre }}
          </v-card-title>

          <v-card-text>
            <v-text-field
              label="Descripcin"
              :rules="[(v) => !!v || 'Es necesaria una descripcin.']"
              required
              v-model="video.descripcion"
            ></v-text-field>
            <v-text-field
              label="URL"
              :rules="[(v) => !!v || 'Es necesaria una URL (direccin).']"
              required
              v-model="video.url"
            ></v-text-field>
          </v-card-text>
          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="error"
              outlined
              @click="
                () => {
                  video = {};
                  isEdit = false;
                }
              "
            >
              Cerrar
            </v-btn>
            <v-btn color="primary" outlined @click="saveVideo()"> Subir </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
    <div class="text-center">
      <v-dialog v-model="videoToDelete.dialog" width="250">
        <v-card>
          <v-card-title class="text-h5 grey lighten-2">
            Borrar vdeo?
          </v-card-title>
          <v-divider></v-divider>
          <v-card-actions justify="center">
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              outlined
              @click="videoToDelete.dialog = false"
            >
              Cerrar
            </v-btn>
            <v-btn color="error" outlined @click="deleteVideo()">
              Borrar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </v-container>
</template>
<style scoped>
#logo {
  font-family: "Wire One";
  font-size: 2em;
}
</style>
<script>
export default {
  data: () => ({
    alert: { show: false },
    admin: { usrname: "" },
    actor: {},
    isEdit: false,
    videoList: [],
    video: { dialog: false },
    videoToEdit: { id: 0 },
    videoToDelete: { dialog: false, id: 0 },
  }),
  created: async function () {
    let session = JSON.parse(sessionStorage.getItem("sesioncda"));
    if (session == null || session.usrname != "admin") {
      this.$router.push("/adminlogin");
    }
    this.admin = session;
    this.axios
      .get(`/actor/get/${this.$route.params.id_a}`)
      .then((res) => {
        this.actor = res.data.actor;
      })
      .catch((e) => {
        console.log(e);
      });
    this.loadVideos(this.$route.params.id_a);
  },
  methods: {
    loadVideos(actor) {
      this.axios
        .get(`/actor/vid/get/${actor}`)
        .then((res) => {
          this.videoList = res.data.vids;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    saveVideo() {
      this.video.id_actor = this.$route.params.id_a;
      this.axios
        .post(`/actor/vid/insert/`, this.video)
        .then((res) => {
          this.video.dialog = false;
          this.video.url = "";
          this.video.descripcion = "";
          this.alert.type = "success";
          this.alert.show = true;
          this.alert.message = res.data.message;
          this.loadVideos(this.$route.params.id_a);
        })
        .catch((e) => {
          this.video.dialog = false;
          this.alert.type = "error";
          this.alert.show = true;
          this.alert.message = e.data.message;
        });
    },
    editVideo(id) {
      this.isEdit = true;
      this.axios
        .get(`/actor/vid/get/one/${id}`)
        .then((res) => {
          this.video = res.data.vid;
          this.video.url = res.data.vid.uri_video;
          this.video.dialog = true;
        })
        .catch((e) => {
          this.video.dialog = false;
          this.alert.type = "error";
          this.alert.show = true;
          this.alert.message = e.data.message;
        });
    },
    deleteVideo() {
      this.axios
        .delete(`/actor/vid/delete/${this.videoToDelete.id}`)
        .then((res) => {
          this.videoToDelete.dialog = false;
          this.videoToDelete.id = null;
          this.alert.type = "success";
          this.alert.show = true;
          this.alert.message = res.data.message;
          this.loadVideos(this.$route.params.id_a);
        })
        .catch((e) => {
          this.video.dialog = false;
          this.alert.type = "error";
          this.alert.show = true;
          this.alert.message = e.data.message;
        });
    },
  },
};
</script>